// This is the simplest implementation of fluid type
// To make this reusable across projects, write some functions to compute the values
// Here's an article that takes you through one implementation
// https://www.aleksandrhovhannisyan.com/blog/fluid-type-scale-with-css-clamp/#programmatically-generating-a-fluid-type-scale
:root {
  --theme-type-size--6: clamp(0.83rem, 0.44vw + 0.72rem, 1rem);
  --theme-type-size--5: clamp(1rem, 0.53vw + 0.87rem, 1.2rem);
  --theme-type-size--4: clamp(1.2rem, 0.64vw + 1.04rem, 1.44rem);
  --theme-type-size--3: clamp(1.44rem, 0.77vw + 1.25rem, 1.73rem);
  --theme-type-size--2: clamp(1.73rem, 0.92vw + 1.5rem, 2.07rem);
  --theme-type-size--1: clamp(2.07rem, 1.11vw + 1.8rem, 2.49rem);
}

